.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}

.modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #c81cc5;
    padding: 1rem 1.5rem;
    width: 24rem;
    color: white;
    border-radius: 0.5rem;
}

.modal-content1 {
    position: absolute;
    min-width: 40%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 1rem 1.5rem;
    color: black;
    border-radius: 0.5rem;
    border-color: black;
}

.modal-content2 {
    overflow: auto;
    position: absolute;
    width: 70%;
    height: 70%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 1rem 1.5rem;
    color: black;
    border-radius: 0.5rem;
    border-color: black;
}

.close-button {
    float: right;
    width: 1.5rem;
    line-height: 1.5rem;
    text-align: center;
    cursor: pointer;
    border-radius: 0.25rem;
    border-color: white;
    border-style: ridge;
}

.close-button:hover {
    background-color: #b01c63;
}

.pad {
    margin: 5px;
}

.rounded {
    border-radius: 5%;
}

.full-width {
    width: 100%;
    height: 450px;
    object-fit: cover;
}

.link-button {
    /*font: bold 11px Arial;*/
    /*text-decoration: none;*/
    /*background-color: #EEEEEE;*/
    color: white;
    padding: 10px 80px 10px 80px;
    border-radius: 14px;
    /*border-top: 1px solid #CCCCCC;*/
    /*border-right: 1px solid #333333;*/
    /*border-bottom: 1px solid #333333;*/
    /*border-left: 1px solid #CCCCCC;*/
    background: linear-gradient(277.13deg, #b01c63 11.98%, #fa0c58 83.77%);
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
}

.header-content div {
    background: var(--primary-btn);
    width: 100%;
    cursor: pointer;
    height: 300px;
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: auto;
}

.header-content div h1 {
    font-family: var(--font-family);
    width: 70%;
    font-weight: bold;
    font-size: 48px;
    line-height: 105.7%;
    color: #ffffff;
    padding: 1rem;
    margin-left: 2rem;
}

.header-content div img {
    width: 17%;
}

.header-slider {
    margin-top: 5rem;
    /* background: wheat; */
    color: #ffffff;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.header-slider .slider {
    width: 70%;
}

.header-slider h1 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 28px;
    line-height: 42px;
    color: #ffffff;
    margin-bottom: 1rem;
}

.slider .slider-card {
    width: 180px !important;
    height: 200px !important;
    background: #2a2d3a;
    border-radius: 20px;
    display: flex !important;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.slider-card img {
    border-radius: 50%;
}

.slider-card-number {
    position: absolute;
    top: 5.5%;
    left: 6.11%;
    background: var(--primary-color);
    width: 33px;
    height: 33px;
    border-radius: 50%;
    font-weight: 600;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slider-card p {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #ffffff;
    margin-top: 5px;
}

.slider-card span {
    font-weight: normal !important;
}

.shake-vertical {
    -webkit-animation: shake-vertical 10s cubic-bezier(0.455, 0.03, 0.515, 0.955) 2s infinite both;
    animation: shake-vertical 10s cubic-bezier(0.455, 0.03, 0.515, 0.955) 2s infinite both;
}

@-webkit-keyframes shake-vertical {
    0%,
    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    10%,
    30%,
    50%,
    70% {
        -webkit-transform: translateY(-8px);
        transform: translateY(-8px);
    }
    20%,
    40%,
    60% {
        -webkit-transform: translateY(8px);
        transform: translateY(8px);
    }
    80% {
        -webkit-transform: translateY(6.4px);
        transform: translateY(6.4px);
    }
    90% {
        -webkit-transform: translateY(-6.4px);
        transform: translateY(-6.4px);
    }
}

@keyframes shake-vertical {
    0%,
    100% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    10%,
    30%,
    50%,
    70% {
        -webkit-transform: translateY(-8px);
        transform: translateY(-8px);
    }
    20%,
    40%,
    60% {
        -webkit-transform: translateY(8px);
        transform: translateY(8px);
    }
    80% {
        -webkit-transform: translateY(6.4px);
        transform: translateY(6.4px);
    }
    90% {
        -webkit-transform: translateY(-6.4px);
        transform: translateY(-6.4px);
    }
}

.slider-img {
    position: relative;
}

.verify {
    position: absolute;
    right: 5px;
    bottom: 2px;
}

/* media  */
@media screen and (max-width: 1600px) {
    .header-slider .slider {
        width: 90%;
    }
}

@media screen and (max-width: 1350px) {
    .header-slider .slider {
        width: 100%;
    }

    .header-content div {
        height: 200px;
    }

    .header-content div h1 {
        font-size: 36px;
        line-height: 32px;
        /* width: 100%; */
    }
}

@media screen and (max-width: 550px) {
    .slick-prev,
    .slick-next {
        display: none !important;
    }

    .header-slider h1 {
        font-size: 20px;
        line-height: 30px;
    }

    .slider .slider-card {
        width: 138px !important;
        height: 165px !important;
    }

    .slider-card-number {
        width: 24px;
        height: 24px;
    }

    .slider-card p {
        font-size: 14px;
        line-height: 21px;
    }

    .header-content div {
        height: 143px;
    }

    .header-content div h1 {
        font-size: 20px;
        line-height: 24px;
        width: 100%;
    }
}

@media screen and (max-width: 800px) {
    .header-content div img {
        width: 20%;
        margin-right: 10px;
    }

    .header-content div {
        justify-content: unset;
    }

    .header-content div h1 {
        margin-left: 1rem;
    }
}

@media screen and (max-width: 650px) {
    .header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }
}

@media screen and (max-width: 400px) {
    .slider .slider-card {
        margin-right: 1rem;
    }

    .bids {
        padding: 2rem 0px !important;
    }
}

@media screen and (max-width: 335px) {
    .slider .slider-card {
        margin-right: 0.5rem !important;
    }
}
